    document01 : 
    "\
    <b>できること</b>\
    <ul>\
      <li>検索された動画を'個々に再生'または'連続再生'できます。</li>\
      <li>動画の投稿日や動画タイトルに含まれる単語から絞り込みできます。</<li>\
    </ul>\
    <b>注意事項</b>\
    <ul>\
      <li>Wi-Fi環境で使用してください。</<li>\
      <li>収録されている情報は、YYYYMMDDに採取されました。</li>\
    </ul>\
    ",
    document02 :
    "\
    <b>動画再生方法</b>\
    <ul>\
      <li>個々に再生する方法\
        <ul>\
          <li>'連続再生する方法'で一個だけ選択します（おすすめ）。</li>\
          <li>または、'URL','イメージ画像'をクリックします。</li>\
        </ul>\
      </li>\
      <li>連続再生する方法\
        <ol>\
          <li>連続再生したい動画の'動画タイトル'をクリックし、\
            <small>\
              <ul>\
                <li>'ALLCHK'を選択すると前後のページを含め全選択できます。</li>\
                <li>'ALLCLR'を選択すると前後のページを含め全解除できます。</li>\
              </ul>\
            </small>\
          </li>\
          <li><b>PLAY</b>ボタンをクリックします。</li>\
          <li>無音で再生されるので、必要に応じ、muteを解除します。</li>\
        </ol>\
      </li>\
    </ul>\
    ",
    document03 :
    "\
    <b>検索方法</b>\
    <ul>\
      <li><b>SEARCH</b>ボタンをクリックし、表示された画面の'search condition'に検索条件を設定します。</li>\
    </ul>\
    <small>'search words...'</small>\
      <ul>\
        <li>検索語を入力します。</li>\
        <li>AND検索する場合は、複数の語を空白で区切って入力します。</li>\
      </ul>\
    <small>'start day'</small>\
      <ul>\
        <li>検索開始日を選択します。</li>\
      </ul>\
    <small>'end day'</small>\
      <ul>\
        <li>検索開始日を選択します。</li>\
      </ul>\
    ",
    document04 :
    "\
    <b>表示変更方法</b>\
    <ul>\
      <li><b>SEARCH</b>ボタンをクリックし、表示された画面の'display mode'の設定を変更することで、表示方法を変更できます。</li>\
    </ul>\
    <small>'Display order'</small>\
      <ul>\
        <li>表示順序を選択します。\
          <ul>\
            <li>'Oldest is first':古い順</li>\
            <li>'Newest is first':新しい順</li>\
            <li>'view count':視聴回数の多い順（おすすめ）</li>\
            <li>'good count':いいねの多い順  </li>\
            <li>'comment count':コメントの多い順</li>\
          </ul>\
        </li>\
      </ul>\
    <small>'Display image'</small>\
      <ul>\
        <li>表示イメージ種を選択します。\
          <ul>\
            <li>'Video       ':動画</li>\
            <li>'thumbnail(L)':サムネイル(大)</li>\
            <li>'thumbnail(S)':サムネイル(小)</li>\
            <li>'none        ':非表示（おすすめ）</li>\
          </ul>\
        </li>\
      </ul>\
    ",
